<template>
  <div class="MarkShow">
    <MavonEditor
        :toolbars="markdownOption"
                 :subfield="false"
                 :defaultOpen="MarkStatus"
                 v-model="myMarkDownData"/>
  </div>
</template>

<script>
// markdown
import {mavonEditor} from 'mavon-editor'

export default {
  name: "ShowMarkDown",
  //
  components: {
    MavonEditor: mavonEditor,
  },
  props:["MyContent"],
  watch:{
    MyContent:{
      immediate:true,
      deep:true,
      handler(newval){
       this.myMarkDownData=newval
      }
    }
  },
  data() {
    return {
      markdownOption: {},
      MarkStatus:"preview",
      myMarkDownData:this.MyContent
    }
  },


}
</script>

<style scoped>
</style>
